@import '../../variables.scss';

.cat {
  @include flex(row, space-between);
  width: 100%;
  box-sizing: border-box;
  padding: 24px 36px;
  gap: 16px;

  @media screen and (max-width: 1024px) {
    & {
      padding: 16px 36px;
    }
  }

  @media screen and (max-width: 769px) {
    & {
      padding: 10px 8px;
      flex-wrap: wrap;
    }
  }

  @media screen and (max-width: 550px) {
    & {
      justify-content: flex-end;
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    gap: 8px;
    overflow: auto;

    @media screen and (max-width: 1024px) {
      & {
        gap: 6px;
      }
    }
  }

  &__item {
    @include flex();
    height: 40px;
    max-height: 48px;
    box-sizing: border-box;
    padding: 14px;
    background: #f9f9f9;
    border-radius: 30px;

    font-weight: 600;
    font-size: 15px;
    line-height: 16px;

    color: $blackLite;

    &_active {
      background-color: $blackLite;
      color: #f9f9f9;
    }

    &:hover {
      cursor: pointer;
      scale: 1.1;
      transition: scale 0.15s ease-in-out;
    }

    &:active {
      transform: scale(0.9);
    }

    @media screen and (max-width: 1024px) {
      & {
        font-size: 12px;
        line-height: 14px;
      }
    }

    @media screen and (max-width: 550px) {
      & {
        font-size: 10px;
        line-height: 12px;
        height: 35px;
      }
    }
  }
}

.sort {
  @include flex(row, space-between);
  gap: 6px;

  position: relative;

  @media screen and (max-width: 1024px) {
    & {
      gap: 4px;
    }
  }

  &__directionButton {
    padding: 0;
    border: none;
    width: 10px;
    height: 6px;
    background-image: url(../../images/sortArrow.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    &_updown {
      transform: rotate(180deg);
    }

    &:hover {
      cursor: pointer;
      scale: 1.1;
      transition: scale 0.15s ease-in-out;
    }

    &:active {
      transform: scale(0.9);
    }
  }

  &__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.015em;
    color: $blackLite;

    @media screen and (max-width: 1024px) {
      & {
        font-size: 10px;
        line-height: 12px;
      }
    }
  }

  &__optionButton {
    padding: 0;
    border: none;
    border-bottom: 1px dashed #fe5f1e;
    background-color: transparent;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.015em;

    color: #fe5f1e;

    &:hover {
      cursor: pointer;
      scale: 1.1;
      transition: scale 0.15s ease-in-out;
    }

    &:active {
      transform: scale(0.9);
    }

    @media screen and (max-width: 1024px) {
      & {
        font-size: 10px;
        line-height: 12px;
      }
    }
  }
}
